@import "../../assets/styles/fonts.css";
@import "../../assets/styles/colors.css";

.mainTitle {
    width: 915px;
    height: 435px;
    margin-bottom: 185px;
    background: url("../../assets/img/Start Fellows.svg") no-repeat;
    background-size: 90%;
}

.aboutPreface {
    font-family: "Manrope-Medium";
    text-transform: uppercase;
    font-size: 20px;
    line-height: 28px;

    color: var(--c-gray);
    margin: 180px 0 16px 0;
}

.aboutText {
    font-family: "Manrope-Light";
    font-size: 40px;
    line-height: 60px;

    color: var(--c-white);
    margin: 0 0 80px 0;
}

.about, .about_today {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 auto 126px auto;
    max-width: 1040px;
}

.about_today {
    margin-bottom: 180px;
}

.about__img, .about__img_today {
    background: url("../../assets/img/Zinger.png") no-repeat;
    background-size: 100%;
    height: 457px;
    width: 457px;
}

.about__img_today {
    background-image: url("../../assets/img/Office.png");
}

.about__text {
    font-family: "Manrope-Light";
    font-size: 24px;
    line-height: 36px;

    color: var(--c-white);
    max-width: 457px;
}

.about__title {
    font-family: "Manrope-Medium";
    font-size: 48px;
    line-height: 56px;

    color: var(--c-white);
    margin-bottom: 32px;
}

.about__first {
    margin-bottom: 16px;
}

@media screen and (max-width: 1200px) {
    .mainTitle {
        margin: 0 0 160px 16px;
        width: 257px;
        height: 136px;
        background: url("../../assets/img/Start Fellows.svg") no-repeat;
    }

    .aboutPreface {
        font-size: 14px;
        line-height: 28px;

        margin: 75px 0 16px 0;
    }

    .aboutText {
        font-size: 24px;
        line-height: 32px;

        max-width: 512px;
        margin-bottom: 56px;
    }

    .about {
        flex-direction: column;
        margin-bottom: 56px;
    }

    .about_today {
        flex-direction: column-reverse;
    }

    .about__img {
        width: 288px;
        height: 180px;
        background: url("../../assets/img/Zinger-mob.png") no-repeat;
        background-size: 100%;
        margin-bottom: 32px;
    }

    .about__img_today {
        width: 288px;
        height: 180px;
        background-size: 100%;
        background-image: url("../../assets/img/Office-mob.png");
        margin-bottom: 24px;
    }

    .about__text {
        font-size: 16px;
        line-height: 28px;

        max-width: 288px;
    }

    .about__title {
        font-size: 30px;
        line-height: 40px;

        margin-bottom: 12px;
    }

    .about__first {
        margin-bottom: 8px;
    }
}

@media screen and (max-width: 500px) {
    .aboutText {
        max-width: 272px;
    }
}